import React from "react";
import { FiArrowRight, FiArrowLeft } from "react-icons/fi";

export const Controls = ({next,prev}) => {
  return (
    <div className="controls">
      <span className="left" onClick={() => prev()}>
        <FiArrowLeft />
      </span>
      <span className="right" onClick={() => next()}>
        <FiArrowRight />
      </span>
    </div>
  );
};
